.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.home-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.dash{
    width: 100%;
    height: 100%;
}

.dash .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top:90%;
    left:16.5%;
    text-align: center;
}

.dash .content h1{
    font-size: 3rem;
    padding: 13rem 0 1.5rem;
}

@media screen and (max-width:640px) {
    .dash .content{
        position: absolute;
        transform: translate(-50%,-50%);
        top:92%;
        left:16.5%;
        text-align: center;
    }
    .dash .content h1{
        font-size: 1.5rem;
    }
}